<template>
    <div>
        <div :class="cptClass">
            <div class="route-item" v-for="(location, index) in cptData">
                <div class="route-text">{{ location.departurePort }}</div>
                <div class="icon-box">
                    <div class="card-way">
                        <svg class="icon" aria-hidden="true">
                            <use
                                v-if="location.trspMode == '空运'"
                                fill="#FFBB32"
                                xlink:href="#icon-andaotongcaidanICON_feiji"
                            />
                            <use
                                v-if="location.trspMode == '铁路'"
                                fill="#FFBB32"
                                xlink:href="#icon-andaotongcaidanICON_huoche"
                            />
                            <use
                                v-if="location.trspMode == '海运'"
                                fill="#FFBB32"
                                xlink:href="#icon-andaotongcaidanICON_lunchuan"
                            />
                        </svg>
                    </div>
                    <div class="card-line">
                        <svg v-if="isBig" class="icon1" aria-hidden="true">
                            <use
                                fill="#02ADB5"
                                xlink:href="#icon-andaotongcaidanICON_changjiantou_changjiantou"
                            />
                        </svg>
                        <svg v-if="!isBig" class="icon" aria-hidden="true">
                            <use fill="#02ADB5" xlink:href="#icon-andaotongcaidanICON_jiantou3" />
                        </svg>
                    </div>
                    <div
                        class="card-genre"
                    >{{ location.cargoType || location.trspType }}.{{ location.logisticsType }}</div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: '',
    props: {
        lineHeaderData: {
            type: Array,
            default: () => {
                let arr = [
                    {
                        departurePort: '重庆',
                        destinationPort: '开州',
                        trspMode: '铁路',
                        cargoType: '普货',
                        logisticsType: '件杂货',
                        sort: 1,
                    },
                    {
                        departurePort: '重庆',
                        destinationPort: '开州',
                        trspMode: '铁路',
                        cargoType: '普货',
                        logisticsType: '件杂货',
                        sort: 2,
                    },
                    {
                        departurePort: '重庆',
                        destinationPort: '开州',
                        trspMode: '铁路',
                        cargoType: '普货',
                        logisticsType: '件杂货',
                        sort: 3,
                    },
                    {
                        departurePort: '重庆',
                        destinationPort: '开州',
                        trspMode: '铁路',
                        cargoType: '普货',
                        logisticsType: '件杂货',
                        sort: 4,
                    },
                    {
                        departurePort: '重庆',
                        destinationPort: '测试',
                        trspMode: '铁路',
                        cargoType: '普货',
                        logisticsType: '件杂货',
                        sort: 5,
                    },
                ]
                return arr
            },
        },
        isBig: {
            type: Boolean,
            default: true,
        },
    },
    data() {
        return {}
    },
    computed: {
        cptData() {
            let end = this.lineHeaderData[this.lineHeaderData.length - 1]
            let itemarr = [
                {
                    departurePort: end.destinationPort,
                },
            ]
            let list = this.lineHeaderData.concat(itemarr)
            return list
        },
        cptClass() {
            if (this.isBig) {
                return 'big-detail-box'
            }
            if (!this.isBig) {
                return 'mini-detail-box'
            }
        },
    },
    created() {},
    mounted() {},
    methods: {
        seePlan() {
            // alert('跳转到方案页面')
            console.log(this.lineHeaderData)
        },
    },
}
</script>

<style scoped lang="scss">
.mini-detail-box {
    padding: 0px 20px;
    background: #f5f5f5;
    height: 76px;
    display: flex;
    align-items: center;
}
.big-detail-box {
    padding: 0px 40px;
    height: 100px;
    background: rgba(255, 255, 255, 1);
    box-shadow: 0px 2px 4px 0px rgba(51, 51, 51, 0.1);
    display: flex;
    align-items: center;
    margin-bottom: 24px;
}
.route-item {
    display: flex;
    align-items: center;
    .route-text {
        font-size: 12px;
        // font-weight: bold;
        white-space: nowrap;
    }
    &:first-child {
        .route-text {
            font-size: 18px;
            font-weight: bold;
        }
    }
    &:last-child {
        .route-text {
            font-size: 18px;
            font-weight: bold;
            color: #999;
        }
    }
    .icon-box {
        display: flex;
        flex-direction: column;
        align-items: center;
        .card-way,
        .card-line {
            height: 10px;
        }
        .card-genre {
            height: 20px;
            color: $light-color;
            text-align: center;
        }
        .icon1 {
            width: 210px;
            height: 20px;
        }
        .icon {
            width: 108px;
            height: 16px;
        }
    }
    &:last-child {
        .icon-box {
            display: none;
        }
    }
}
</style>
